	module.exports = {"data":{"markdownRemark":{"html":"<h2>Sleep How Can I Make a Time Delay in Python</h2>\n<p>The <code>time</code> module in the Python standard library contains the function <code>sleep()</code> which suspends a program for a given number of seconds.</p>\n<p>Example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>import time\n\nfor letter in 'hello, world!':\n    print(letter)\n    time.sleep(2)  # sleep 2 seconds between each print</code></pre>\n      </div>\n<p>Floating point numbers can be given as the argument to <code>sleep()</code> for more precise sleep times.</p>\n<h4>More Information:</h4>\n<p>Time module <a href='https://docs.python.org/3/library/time.html#time.sleep' target='_blank' rel='nofollow'>documentation</a> on the sleep function.</p>","fields":{"slug":"/python/sleep-how-can-i-make-a-time-delay-in-python/"},"frontmatter":{"title":"Sleep How Can I Make a Time Delay in Python"}}},"pathContext":{"slug":"/python/sleep-how-can-i-make-a-time-delay-in-python/"}}

